import React, {Component} from 'react';
import {
    Page,
    Navbar,
    NavLeft,
    NavTitle,
    NavTitleLarge,
    NavRight,
    Link,
    Toolbar,
    Block,
    BlockTitle,
    List,
    ListItem,
    Row,
    Col,
    Button,
    Searchbar,
    View,
    LoginScreenTitle,
    ListInput,
    ListButton,
    BlockFooter
} from 'framework7-react';

import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as Actions from '../store/actions';

class login extends Component {
    state = {
        error: null,
        username: '',
        password: ''
    }

    backClick = () => {

        let properti = 'beranda';
        // alert('tes');
        // console.log(this.props.f7router.url.replace("/","").replace("/",""));
        // console.log(this.props.tabBar);
        for (var property in this.props.tabBar) {
            // console.log(this.state.tabBar[property]);
            this.props.tabBar[property] = false;
        }
        if(this.props.f7router.url.replace("/","").replace("/","") !== ""){
            properti = this.props.f7router.url.replace("/","").replace("/","");
        }
        this.props.tabBar[properti] = true;

        this.props.setTabActive(this.props.tabBar);
        console.log(this.props.tabBar.beranda);
    }

    alertLoginData = () => {
        this.$f7.dialog.alert('Username: ' + this.state.username + '<br>Password: ' + this.state.password);
    }

    render()
    {
        return (
            <View>
                <Page loginScreen>
                    <LoginScreenTitle>Masuk Ajarin</LoginScreenTitle>
                    <List form>
                        <ListInput
                        type="text"
                        name="username"
                        placeholder="Username Anda ...."
                        label= 'Username'
                        value={this.state.username}
                        onInput={(e) => this.setState({username: e.target.value})}
                        ></ListInput>
                        <ListInput
                        label= 'Password'
                        type="password"
                        name="password"
                        placeholder="Password Anda ...."
                        value={this.state.password}
                        onInput={(e) => this.setState({password: e.target.value})}
                        ></ListInput>
                    </List>
                    <List style={{padding:16}}>
                        <Button fill 
                            iconIos="f7:enter_fill" 
                            iconAurora="f7:enter_fill" 
                            iconMd="material:enter"  
                            title="Masuk" 
                            loginScreenClose 
                            onClick={() => this.alertLoginData()}
                            style={{paddingTop:10,paddingBottom:10, height:50}}
                        >
                            &nbsp;&nbsp; Masuk Aplikasi
                        </Button>
                    </List>
                    <List style={{padding:16}}>
                        <BlockTitle>
                            Belum punya akun?
                        </BlockTitle>
                        <Button fill 
                            // iconIos="f7:enter_fill" 
                            // iconAurora="f7:enter_fill" 
                            // iconMd="material:enter"  
                            title="Masuk" 
                            color="green"
                            loginScreenClose 
                            onClick={() => this.alertLoginData()}
                            style={{paddingTop:10,paddingBottom:10, height:50}}
                        >
                            Daftar Pengguna Baru
                        </Button>
                    </List>
                </Page>
            </View>
        )
    }
}

function mapDispatchToProps(dispatch) {
    return bindActionCreators({
      updateWindowDimension: Actions.updateWindowDimension,
      setLoading: Actions.setLoading,
      setTabActive: Actions.setTabActive
    }, dispatch);
}

function mapStateToProps({ App }) {
    return {
        window_dimension: App.window_dimension,
        loading: App.loading,
        tabBar: App.tabBar
    }
}

export default (connect(mapStateToProps, mapDispatchToProps)(login));
  